<template>
  <div id="app">
    <MyLoading v-if="loading.value" />
    <router-view></router-view>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  name: "App",
  components: {
    MyLoading : () => import('@/components/MyLoading'),
  },
  data() {
    return {
      token: localStorage.getItem("apollo-token"),
      loading : {
        value : false,
      }
    };
  },
   provide() {
    return {
      loading: this.loading,
    };
  },
  async mounted() {
    // Every home element mounted search query
    if (this.token) {
      try {
        await this.userQuery();
      } catch (err) {
        this.userLogout();
        this.$router.push({
          name: "error",
          params: {
            msg: "error-token",
          },
        });
      }
    }
  },
  methods: {
    ...mapActions(["userLogout", "userQuery"]),
  },
};
</script>

