<template>
  <div class="header">
    <nav class="navigation">
      <h1 class="title-lg">Zuans</h1>
      <BurgerItem />
    </nav>
    <SideBarItem>
      <h2 class="sidebar-title">Zuans Book</h2>
      <div v-if="user" class="user-profile">
        <img
          :src="user.avatar"
          :class="{ 'img-avatar': user.avatar }"
          id="avatar"
          alt
        />
        <h3>Hello {{ user.username }}</h3>
      </div>
      <ul class="sidebar-list">
        <li>
          <a href="/" @click="togglePanel(false)">Home</a>
        </li>
        <li>
          <a @click="toggleFeature">
            Book
            <font-awesome
              class="ml-1"
              :icon="['fas', 'caret-down']"
            ></font-awesome>
          </a>
          <transition name="showFeature">
            <div v-if="showFeature" class="pt-3">
              <li>
                <a href="/feature/book-add" @click="togglePanel(false)">Add</a>
              </li>
              <li>
                <a href="/feature/book-search" @click="togglePanel(false)"
                  >Search</a
                >
              </li>
              <li>
                <a href="/feature/book-all" @click="togglePanel(false)"
                  >List Book</a
                >
              </li>
            </div>
          </transition>
        </li>
        <li>
          <a href="/#about" @click="togglePanel(false)">About</a>
        </li>
        <li v-if="user">
          <a href="/feature/book-like" @click="togglePanel(false)">Wishlist</a>
        </li>
        <li
          v-if="auth.userType == 'admin' || getUser.user_type == 'admin' "
        >
          <a href="/admin/admin-info">Admin</a>
        </li>
        <li>
          <button
            v-if="!user"
            @click="goTo('login')"
            class="btn btn-success px-4 py-2"
          >
            Login
          </button>
          <button
            v-else
            @click="goTo('logout')"
            class="btn btn-outline-light px-4 py-2"
          >
            Logout
          </button>
        </li>
      </ul>
    </SideBarItem>
  </div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";
import BurgerItem from "./side-menu/MenuBurger";
import SideBarItem from "./side-menu/MenuSide";

export default {
  name: "myHeader",
  data() {
    return {
      dataUser: "",
      showFeature: false,
    };
  },
  components: {
    BurgerItem,
    SideBarItem,
  },
  methods: {
    ...mapActions(["userLogout"]),
    ...mapMutations(["togglePanel"]),
    goTo(url) {
      if (url == "logout") {
        const username = this.getUser.username;
        this.userLogout(username);
        this.dataUser = false;
        return this.$router.go();
      }
      this.togglePanel(false);
      this.$router.push({ name: `${url}` });
    },
    toggleFeature() {
      this.showFeature = !this.showFeature;
    },
  },
  computed: {
    ...mapState(["auth"]),
    ...mapState({ getUser: "user" }),
    user() {
      if (localStorage.getItem("logIn")) {
          const {
            avatar: { filename },
            username,
          } =  this.getUser;
          return {
            avatar : `/img-avatar/${filename}`,
            username,
          }
      }
      return null;
    }
  },
};
</script>

