import Vue from 'vue'
import App from './App.vue'
import VueApollo from 'vue-apollo';

import {
  BootstrapVue
} from 'bootstrap-vue'
import VueRouter from 'vue-router';

// Import style bootstrap
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap-vue/dist/bootstrap-vue.css'

// Fontawesome
import {
  library
} from '@fortawesome/fontawesome-svg-core';

// Font awesome icon
import {
  faUserSecret,
  faUsersCog,
  faTimes,
  faPlus,
  faSearch,
  faCopyright,
  faLevelDownAlt,
  faBook,
  faHeart,
  faEnvelope,
  faCaretDown,
  faFileAlt,
  faUserEdit,
  faEdit,
  faTrashAlt,
  faBookOpen
} from '@fortawesome/free-solid-svg-icons';


// font awesome icon brand
import {
  faFacebook as fabFacebook,
  faGithub as fabGithub,
  faInstagram as fabInstagram
} from '@fortawesome/free-brands-svg-icons';

import {
  FontAwesomeIcon
} from '@fortawesome/vue-fontawesome';

// Impoort router
import router from './router';

// Import Store
import store from './store/index';

// Import apollo config  
import {
  apolloProvider
} from './vue-apollo'

import '@/plugin/echarts';



library.add(faUserSecret, 
  faTimes, 
  faBook, 
  faPlus, 
  faSearch, 
  faCopyright, 
  faLevelDownAlt, 
  faHeart, 
  faEnvelope, 
  faCaretDown, 
  faFileAlt, 
  faEdit,
  faTimes,
  faUserEdit,
  faUsersCog, 
  faTrashAlt,  
  faBookOpen,
  fabGithub, 
  fabFacebook, 
  fabInstagram
);
Vue.component('font-awesome', FontAwesomeIcon);

Vue.use(VueApollo);
Vue.use(VueRouter);
Vue.use(BootstrapVue);
Vue.config.productionTip = false

new Vue({
  router,
  store,
  apolloProvider,
  render: h => h(App)
}).$mount('#app')