import {
    apolloClient
} from '../vue-apollo';

import {
    AUTH_USER,
    QUERY_USER,
    LOGOUT_USER
} from '../graphql/query';

import {
    REGISTER_USER,
} from '../graphql/mutations'


export default {
    userQuery: async ({
        commit,
        state
    }) => {
        try {
            const {
                data: {
                    user
                }
            } = await apolloClient.query({
                query: QUERY_USER,
            })
            await commit('SET_USER', user);
            return state.user;
        } catch (err) {
            throw new Error(err);
        }
    },

    userAuth: async ({
        commit,
        dispatch,
    }, authDetails) => {
        try {
            const {
                data: {
                    authUser
                }
            } = await apolloClient.query({
                query: AUTH_USER,
                variables: authDetails,
            });
            await commit('SET_AUTH', authUser);
            await dispatch('userQuery');
        } catch (error) {
            throw new Error(error);
        }
    },
    userRegister: async ({
        dispatch
    }, inputUser) => {
        const {
            username,
            email,
            password,
            avatar
        } = inputUser;
        let avatarImg = avatar;
        if(!avatar) {
            avatarImg = await dispatch('setImgDefault');
        }
        try {
            await apolloClient.mutate({
                mutation: REGISTER_USER,
                variables: {
                    username,
                    email,
                    password,
                    avatarImg
                },
                hasUpload : true                
            });
        } catch (error) {
            throw new Error(error);
        }
    },
    async userLogout({commit},username){
        try {
           await apolloClient.query({ 
                query: LOGOUT_USER, 
                variables : { username },
            });
        } catch (error) {
            console.error(error);
        }
        commit('LOGOUT_USER');
    },

    async setImgDefault(){
        const image = await fetch('https://res.cloudinary.com/zuans/image/upload/v1600507046/avatar-gacha/download8_x6hnkg.jpg',{
            method : 'GET',
        }).then( _ => _.blob())
        .catch(err => console.log(err));
        const file = new File([image],'default.jpg',{ type : 'image/jpg', lastModified: new Date().getTime });
        return file;
    }
}