import VueRouter from 'vue-router';

// import routes component
import Home from './routes/Home';
import Auth from './routes/Auth';
import Feature from './routes/Feature';



const router = new VueRouter({
    mode: 'history',
    routes: [{
            path: '/',
            name: 'home',
            component: Home

        },
        {
            path: '/auth',
            name: 'auth',
            component: Auth,
            children: [{
                    path: 'login/:error',
                    name: 'loginError',
                    component: () => import('./components/form-auth/FormLogin'),
                    meta: {
                        hasAuth: "false"
                    }
                },
                {
                    path: 'login',
                    name: 'login',
                    component: () => import('./components/form-auth/FormLogin'),
                    meta: {
                        hasAuth: "false"
                    }
                },
                {
                    path: 'register',
                    name: 'register',
                    component: () => import('./components/form-auth/FormRegister'),
                    meta: {
                        hasAuth: "false"
                    }
                },
                {
                    path : 'verify-account/:token',
                    name : 'verify-account',
                    component: () => import('./components/form-auth/VerifyToken'),
                    meta : {
                        hasAuth : "false",
                    }
                },
                {
                    path : 'success-update',
                    name : 'success-update',
                    component : () => import('./components/form-auth/SuccessUpdate'),
                }
            ]
        },
        {
            path: '/feature',
            name: 'feature',
            component: Feature,
            children: [{
                    path: 'book-search',
                    name: 'book-search',
                    component: () => import('./components/feature/BookSearch'),
                },
                {
                    path: 'book-add/:success',
                    name: 'book-add-success',
                    component: () => import('./components/feature/BookAdd'),
                },
                {
                    path: 'book-add',
                    name: 'book-add',
                    component: () => import('./components/feature/BookAdd'),
                    meta: {
                        hasAuth: true,
                    }
                },
                {
                    path: 'book-all',
                    name: 'book-all',
                    component: () => import('./components/feature/BookAll'),
                },
                {
                    path: 'book-detail/:bookId',
                    name: 'book-detail',
                    component: () => import('./components/feature/BookDetail'),
                },
                {
                    path: "book-like",
                    name: "book-like",
                    component: () => import('./components/feature/BookLike')
                },
            ]
        },
        {
            path: '/admin',
            name: 'admin',
            component: () => import('./routes/Admin'),
            children: [
                {
                    path: 'admin-info',
                    name: 'admin-info',
                    component: () => import('./routes/admin/AdminInfo'),
                },
                {
                    path: 'admin-account',
                    name : 'admin-account',
                    component : () => import('./routes/admin/AdminAccount'),
                },
                {
                    path: 'admin-book',
                    name: 'admin-book',
                    component: () => import('./routes/admin/AdminBook'),
                },
                {
                    path: 'admin-book/:status',
                    name: 'admin-book-status',
                    component: () => import('./routes/admin/AdminBook'),
                },
                {
                    path: 'admin-book-edit/:bookId/:page',
                    name: 'admin-book-edit',
                    component: () => import('./routes/admin/BookEdit'),
                },
                {
                    path: 'admin-author',
                    name: 'admin-author',
                    component: () => import('./routes/admin/AdminAuthor'),
                },
                {
                    path: 'admin-author/:status',
                    name: 'admin-author-status',
                    component: () => import('./routes/admin/AdminAuthor'),
                },
                {
                    path : 'admin-author-form/:action/:authorId',
                    name : 'admin-author-form',
                    component : () => import('./routes/admin/AuthorForm'),
                },
            ]
        },

        {
            path: '/error/:msg',
            name: 'error',
            component: () => import('./routes/Error'),
        },
    ]
})

router.beforeEach((to, from, next) => {
    // check if user login
    const isLogin = localStorage.getItem('logIn');
    const token = localStorage.getItem('apollo-token');
    // Route where user must not auth
    if (to.matched.some(record => record.meta.hasAuth == "false")) {
        if (isLogin && token) {
            return next({
                name: 'home',
            })
        } else {
            return next()
        }
        // Route for user with auth
    } else if (to.matched.some(record => record.meta.hasAuth)) {
        if (!isLogin && !token) {
            return next({
                name: 'login',
                params: {
                    error: "Please Login or register to get more feature"
                }
            })
        } else {
            return next();
        }
    } else {
        return next()
    }
})


router.beforeEach((to, from, next) => {
    if (!to.matched.length) {
        next('/error/not-found');
    } else {
        next();
    }
})

export default router;