<template>
  <div id="burger" :class="{ 'active' : isBurgerOpen }" @click.prevent="toggle">
    <slot>
      <button class="button-burger">
        <div></div>
      </button>
    </slot>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  name: "burgerItem",
  data() {
    return {
      isBurgerActive: false,
    };
  },
  methods: {
    ...mapMutations(["togglePanel"]),
    toggle() {
      this.isBurgerActive = !this.isBurgerActive;
      return this.togglePanel(this.isBurgerActive);
    },
  },
  computed: {
    ...mapState({ isBurgerOpen: "isPanelOpen" }),
  },
};
</script>

<style lang="scss" scoped>
@import "~@/assets/scss/_variables.scss";
@import "~@/assets/scss/_mixins.scss";

.hidden {
  visibility: hidden;
}

#burger {
  z-index: 999;
  position: fixed;
  left: 94%;
  @include respond(small) {
    left: 80%;
  }
}

.button-burger {
  border: none;
  width: 80px;
  height: 80px;
  background-color: transparent;
  padding: 1em;
  z-index: 999;
  div {
    position: relative;
    width: 100%;
    height: 2px;
    background-color: $warning-color-lg;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.7s ease;
  }

  div::before,
  div::after {
    content: "";
    position: absolute;
    top: 0;
    width: 100%;
    height: 2px;
    margin-top: -15px;
    background-color: $warning-color-lg;
  }

  div::after {
    margin-top: 15px;
  }
}

button:focus {
  outline: none;
}

.active .button-burger {
  div {
    transform: rotate(135deg);
  }

  div::after,
  div::before {
    transform: rotate(90deg);
    margin: auto;
  }
}
</style>