export default {
    SET_AUTH(state, user) {
        state.auth = {
            ...user
        };
        state.auth.logIn = true;
        localStorage.setItem('apollo-token', user.accessToken);
        localStorage.setItem('logIn', true);
    },

    SET_USER(state, user) {
        state.user = {
            ...user
        };
    },

    SET_ERROR(state, error) {
        state.error = error;
    },

    LOGOUT_USER(state) {
        state.auth = {};
        localStorage.removeItem('apollo-token');
        localStorage.removeItem('logIn');
        state.user = false;
    },

    togglePanel(state, status) {
        return state.isPanelOpen = status;
    }
}