const middlewareAuth = new ApolloLink((operation, forward) => {
  operation.setContext({
    headers: {
      'authorization': localStorage.getItem('apollo-token') ? `Bearer ${localStorage.getItem('apollo-token')}` : null
    }
  })
  return forward(operation);
})




// For producition

const uploadLink = createUploadLink({
  uri: '/graphql',
})


// For developement
// const uploadLink = createUploadLink({
//   uri: 'http://localhost:5000/graphql',
// })


const link = middlewareAuth.concat(uploadLink);


// Setting apollo client
export const apolloClient = new ApolloClient({
  link,
  cache: new InMemoryCache()
})

export const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
})